import React from 'react'
import {graphql} from "gatsby"

const Project = ({ data }) => {
  return (
    <div>
    {data.datoCmsProject.title}
    </div>
  )
}

export const query = graphql`
query getProject ($slug: String) {
  datoCmsProject(slug: {eq: $slug}) {
    title
    body {
      text
    }
  }
}

`
export default Project
